import React from 'react'
import { Link, StaticQuery, graphql } from 'gatsby';
import Layout from 'components/Layout'
import styled from 'styled-components'
import Img from 'gatsby-image';
import PropTypes from 'prop-types';
import HowSection from 'components/How/HowSection'

import Channelling from 'components/Channelling'
import theme from '@style/theme'
import { ButtonArrow, Container, H2, H3 } from '@style/global'
import Meta from 'components/Meta'
import Popup from 'reactjs-popup'

import { IconClose } from 'components/Icon/IconClose'
//import ScrollAnimation from 'react-animate-on-scroll'


const PopUpImg = ({children, caption, link}) =>  (
    <StyledPopup
        trigger={
            <Art>
            {children}
            </Art>
        }
        modal
        closeOnDocumentClick
      >
      {close => (
        <PopHover>
          <Close onClick={close}>
            <IconClose/>
          </Close>
          {children}
          <Caption>
            <p>{caption}</p>
            {link ? (
              <ButtonArrow><Link to={link}>Learn more</Link></ButtonArrow>
            ) : (
              ''
            )}
          </Caption>
        </PopHover>
      )}
    </StyledPopup>
);

PopUpImg.propTypes = {
  children: PropTypes.element.isRequired,
};


