export default How


const HowPage = styled.div`
  padding: 20px 0 35px 0;
  background-color: ${theme.color.primary.S100D};
  ${H2}, ${H3} {
    font-weight: bold;
  }
`

const Grid = styled.div`
  display: grid;
  grid-template-columns: 2fr 4fr;
  grid-gap: 42px;
  margin: 100px 0 0 0;
  @media (max-width: ${theme.screen.md}) {
    margin: 30px 0;
    grid-template-columns: 1fr;
  }
`

const Info = styled.div`
  padding-right: 25%;
  @media (max-width: ${theme.screen.md}) {
    padding-right: 0%;
    margin-top: 30px;
  }
`
const Art = styled.figure`
  margin: 0;
  max-width: 1380px;
  width: 100%;
  img{
    width: 100%;
    mix-blend-mode: luminosity;
  }    
  background: linear-gradient(270deg, #92CEEF, #04664E);
  background-size: 400% 400%;
  -webkit-animation: AnimationName 5s ease infinite;
  -moz-animation: AnimationName 5s ease infinite;
  animation: AnimationName 5s ease infinite;
  @-webkit-keyframes AnimationName {
      0%{background-position:0% 50%}
      50%{background-position:100% 50%}
      100%{background-position:0% 50%}
  }
  @-moz-keyframes AnimationName {
      0%{background-position:0% 50%}
      50%{background-position:100% 50%}
      100%{background-position:0% 50%}
  }
  @keyframes AnimationName {
      0%{background-position:0% 50%}
      50%{background-position:100% 50%}
      100%{background-position:0% 50%}
  }
`;

const ImgGrid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  overflow: hidden;
  grid-gap:15px;
  text-align: left;
  align-items: center;
  justify-items: center;    
  height: 50px;
  transition: all 0.5s;
  :hover{
    height: 100px;
  }
  @media (max-width: ${theme.screen.md}) {
    grid-template-columns: 1fr 1fr;
    margin-bottom: 96px;
    grid-gap: 15px;
    &:last-child {
      margin-bottom: 0px;
    }
  }
  > ${Art}{
    cursor: pointer;
    img{
      object-fit: none;
      object-position: center;  
    }
  }
`;

const Close = styled.div`
  position: absolute;
  cursor: pointer;
  display: block;
  float: right;
  top: 5px;
  right: 0px;
  z-index: 10;
  color: ${theme.color.primary.S1000D};
  background-color: ${theme.color.white};
  @media (max-width: ${theme.screen.lg}) {
    padding: 16px;
    z-index: 999;
    top: 0px;
    left: auto;
    right: 0;
    position: fixed;
    color: ${theme.color.primary.S1000D};
  }
  svg {
    transition: 0.3s;
  }
  svg:hover {
    -webkit-transform: rotate(90deg); /* Chrome, Safari, Opera */
    transform: rotate(90deg);
  }
`
const StyledPopup = styled(Popup)`
  &-overlay {
    background: rgba( 255, 255, 255, 0.15) !important;
    z-index: 99 !important;
    -webkit-animation: anvil 0.3s cubic-bezier(0.38, 0.1, 0.36, 0.9) forwards;
    @media (max-width: ${theme.screen.md}) {
      border: 7px solid ${theme.color.primary.S100D};
      background: rgba( 255, 255, 255, 1) !important;
    }
  }
  &-content {
    background: rgba(0, 0, 0, 0) !important;
    border: none !important;
    width: auto !important;
    height: auto !important;
    margin: 70px !important;
    padding: 0px !important;
    display: grid;
    align-content: center;
    justify-content: center;
    @media (max-width: ${theme.screen.lg}) {
      margin: 0px !important;
      align-content: flex-start;
    }
  }
  /* animation */

  @keyframes anvil {
    0% {
      transform: translateY(0px);
      opacity: 0;
      box-shadow: 0 0 0 rgba(241, 241, 241, 0);
    }
    1% {
      transform: translateY(10px);
      opacity: 0;
      box-shadow: 0 0 0 rgba(241, 241, 241, 0);
    }
    100% {
      transform: =translateY(0px);
      opacity: 1;
      box-shadow: 0 0 500px rgba(241, 241, 241, 0);
    }
  }
`

const PopHover = styled.div`
  display: grid;
  height: 100% !important;
  grid-template-columns: 3fr 1fr;
  padding: 0 !important;
  margin: 0 !important;
  @media (max-width: ${theme.screen.lg}) {
    grid-template-columns: unset !important;
    height: auto !important;
  }
`

const Caption = styled.div`
  margin: 0;
  padding: 30px 0 0 15px;
  background: ${theme.color.white};
  @media (max-width: ${theme.screen.md}) {
    padding: 10px;
  }
`